@import url(https://fonts.googleapis.com/css?family=Lato:100&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{scroll-behavior:smooth;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;-webkit-tap-highlight-color:transparent}.app .vid{position:relative;font-size:50px}.app .vid .video{height:100vh;width:100%;object-fit:cover;display:block}.app .vid .container{position:absolute;top:47%;margin:0 auto;width:100%;text-align:center}button{transition:all .5s ease;color:#fff;border:3px solid #fff;text-transform:uppercase;text-align:center;line-height:1;font-size:3rem;background-color:transparent;padding:15px;outline:none;border-radius:4px}button:hover{color:#000;background-color:#fff}.main{background-color:#292c2f}
.gallery{-webkit-column-count:3;column-count:3;-webkit-column-width:33%;column-width:33%;scroll-behavior:smooth;-ms-scroll-snap-type:y mandatory;scroll-snap-type:y mandatory;transition:all 1s ease}.gallery .pics{transition:all 350ms ease;cursor:pointer;margin:0px -8px -4px -8px;overflow:hidden;resize:.2}.gallery .pics img{transition:transform .5s,-webkit-filter 1.5s ease-in-out;transition:transform .5s,filter 1.5s ease-in-out;transition:transform .5s,filter 1.5s ease-in-out,-webkit-filter 1.5s ease-in-out;-webkit-filter:grayscale(100%);filter:grayscale(100%)}.gallery .pics img:hover{-webkit-filter:grayscale(0);filter:grayscale(0);transform:scale(1.1)}@media(max-width: 991px){.gallery{-webkit-column-count:2;column-count:2}}@media(max-width: 480px){.gallery{-webkit-column-count:1;column-count:1;-webkit-column-width:100%;column-width:100%}}
*{font-family:Roboto}section{width:100%;display:inline-block;background:#ccc;height:60vh;text-align:center;font-size:22px;font-weight:700;text-decoration:underline}.footer-distributed{background-color:#292c2f;box-shadow:0 1px 1px 0 rgba(0,0,0,.12);box-sizing:border-box;width:100%;text-align:left;font:normal 16px sans-serif;padding:45px 50px}.footer-distributed .footer-left p{color:#8f9296;font-size:14px;margin:0}.footer-distributed p.footer-links{font-size:22px;font-weight:bold;color:#fff;margin:0 0 10px;padding:0;transition:ease .25s;text-decoration:none}.footer-distributed p.footer-links a{display:inline-block;line-height:1.8;text-decoration:none;color:inherit;transition:ease .25s}.footer-distributed .footer-links a:before{content:"·";font-size:20px;left:0;color:#fff;display:inline-block;padding-right:5px;text-decoration:none}.footer-distributed .footer-links .link-1:before{content:none;text-decoration:none}.footer-distributed .footer-right{float:right;margin-top:6px;max-width:180px}.footer-distributed .footer-right a{display:inline-block;width:35px;height:35px;border-radius:2px;font-size:30px;color:#fff;text-align:center;line-height:35px;margin-left:3px;transition:all .25s}.footer-distributed .footer-right a:hover{transform:scale(1.1);-webkit-transform:scale(1.1)}.footer-distributed p.footer-links a:hover{text-decoration:none}@media(max-width: 600px){.footer-distributed .footer-left,.footer-distributed .footer-right{text-align:center}.footer-distributed .footer-right{float:none;margin:0 auto 20px}.footer-distributed .footer-left p.footer-links{line-height:1.8}}
